:root {
  --sky: #caebf2;
  --carbon: #a9a9a9;
  --watermelon: #ff3b3f;
  --neutral: #efefef;
  --almostWhite: #fafafa;
  --fresh: #4abdac;
  --niceGrey: #edeeef;
  --darkGrey: #222222;
  --whiteFont: #dcdcdc;
  --hoverGrey: #3c3c3c;
  --font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Open Sans", "Helvetica Neue", "Ubuntu", sans-serif;

  --linkHover: #ffffff;

  --headerHeight: 50px;
  --footerHeight: 350px;
  --tabHeight: 27px;

  --backgroundOdd: var(--niceGrey);
  --backgroundEven: white;
  --backgroundEducation: lighten($darkGrey, 5%);
  /* TODO: redo the main text color */
  --mainText: #5d5d5d;
  --mainLighterText: #5d5d5d;
  --mainLightestText: #5d5d5d;
  --subText: var(--carbon);
  --phoneMinWidth: 320px;
  --phoneMaxWidth: 479px;
  --padMinWidth: 480px;
  --padMaxWidth: 767px;
  --desktopMinWidth: 768px;
}

/* $headerColor: lighten($carbon, 20%); */

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 50px;
  height: var(--headerHeight);
  opacity: 0.93;
  background-color: #222222;
  background-color: var(--darkGrey);
  z-index: 100;
}

.header__elem {
  display: flex;
  align-items: center;
  color: #dcdcdc;
  color: var(--whiteFont);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Open Sans", "Helvetica Neue", "Ubuntu", sans-serif;
  padding-left: 30px;
  padding-right: 30px;
  cursor: pointer;
  font-weight: bold;
  font-size: 16px;
  height: 100%;
}

.header__elem:hover {
  color: #ffffff;
  color: var(--linkHover);
  background-color: #3c3c3c;
  background-color: var(--hoverGrey);
  border-bottom: none;
}

.header__githubLink {
  margin-left: auto;
  margin-right: 20px;
}

.header svg {
  height: 32px;
  width: 32px;
  cursor: pointer;
  color: #a9a9a9;
  color: var(--carbon);
}

.header svg:hover {
  color: #ffffff;
  color: var(--linkHover);
}
