.tabArea {
  position: relative;
}
@media (max-width: 479px) {
  .tabArea {
    width: 300px;
    height: 320px;
  }
}
@media (min-width: 480px) and (max-width: 767px) {
  .tabArea {
    width: 600px;
    height: 500px;
  }
}
@media (min-width: 768px) {
  .tabArea {
    width: 600px;
    height: 500px;
  }
}
.tabArea > .animate-tabs {
  -webkit-animation-name: grow;
          animation-name: grow;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
}
.tabArea__content {
  display: flex;
  justify-content: center;
  height: calc(100% - var(--tabHeight));
  flex-direction: column;
}
.tabArea__tabs {
  display: flex;
  justify-content: center;
  width: 100%;
}
.tabArea__tab {
  display: inline-block;
  padding: 2px 7px;
  color: grey;
  border: 1px solid lightgrey;
  border-radius: 6px;
  cursor: pointer;
  margin: 0 3px;
  box-shadow: 1px 1px 2px 0px rgba(0,0,0,0.75);
  background-color: var(--backgroundOdd);
}
.tabArea__tab.selected {
  font-weight: bold;
  background-color: var(--backgroundEven);
  border-color: var(--watermelon);
  color: var(--watermelon);
}
.tabArea__selectedLine {
  width: 100%;
  border-bottom: none;
  padding-top: 2px;
}
