:root {
  --sky: #caebf2;
  --carbon: #a9a9a9;
  --watermelon: #ff3b3f;
  --neutral: #efefef;
  --almostWhite: #fafafa;
  --fresh: #4abdac;
  --niceGrey: #edeeef;
  --darkGrey: #222222;
  --whiteFont: #dcdcdc;
  --hoverGrey: #3c3c3c;
  --font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Open Sans", "Helvetica Neue", "Ubuntu", sans-serif;

  --linkHover: #ffffff;

  --headerHeight: 50px;
  --footerHeight: 350px;
  --tabHeight: 27px;

  --backgroundOdd: var(--niceGrey);
  --backgroundEven: white;
  --backgroundEducation: lighten($darkGrey, 5%);
  /* TODO: redo the main text color */
  --mainText: #5d5d5d;
  --mainLighterText: #5d5d5d;
  --mainLightestText: #5d5d5d;
  --subText: var(--carbon);
  --phoneMinWidth: 320px;
  --phoneMaxWidth: 479px;
  --padMinWidth: 480px;
  --padMaxWidth: 767px;
  --desktopMinWidth: 768px;
}

.app-root {
  max-width: 100%;
}

.app-main {
}

body {
  margin: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Open Sans", "Helvetica Neue", "Ubuntu", sans-serif;
  font-family: var(--font);
  background-color: #222222;
  background-color: var(--darkGrey);
  overflow-x: hidden;
}

h1 {
  margin: 0px;
  font-size: 48px;
  color: #5d5d5d;
  color: var(--mainText);
  font-weight: 200;
}

h2 {
  margin: 0px;
  font-size: 24px;
  color: #5d5d5d;
  color: var(--mainLighterText);
  font-weight: 200;
}

h3 {
  margin: 0px;
  font-size: 20px;
  color: #5d5d5d;
  color: var(--mainLightestText);
  font-weight: 200;
}

h4 {
  margin: 0px;
  font-size: 18px;
  color: #5d5d5d;
  color: var(--mainLightestText);
  font-weight: 200;
}

@media --mobile {
  h1 {
    font-size: 34px;
  }

  h2 {
    font-size: 18px;
  }

  h3 {
    font-size: 16px;
  }

  h4 {
    font-size: 14px;
  }
}

@media --pad {
  h1 {
    font-size: 42px;
  }

  h2 {
    font-size: 21px;
  }

  h3 {
    font-size: 18px;
  }

  h4 {
    font-size: 16px;
  }
}
