:root {
  --sky: #caebf2;
  --carbon: #a9a9a9;
  --watermelon: #ff3b3f;
  --neutral: #efefef;
  --almostWhite: #fafafa;
  --fresh: #4abdac;
  --niceGrey: #edeeef;
  --darkGrey: #222222;
  --whiteFont: #dcdcdc;
  --hoverGrey: #3c3c3c;
  --font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Open Sans", "Helvetica Neue", "Ubuntu", sans-serif;

  --linkHover: #ffffff;

  --headerHeight: 50px;
  --footerHeight: 350px;
  --tabHeight: 27px;

  --backgroundOdd: var(--niceGrey);
  --backgroundEven: white;
  --backgroundEducation: lighten($darkGrey, 5%);
  /* TODO: redo the main text color */
  --mainText: #5d5d5d;
  --mainLighterText: #5d5d5d;
  --mainLightestText: #5d5d5d;
  --subText: var(--carbon);
  --phoneMinWidth: 320px;
  --phoneMaxWidth: 479px;
  --padMinWidth: 480px;
  --padMaxWidth: 767px;
  --desktopMinWidth: 768px;
}

.profileArea > img {
  width: 100%;
}

.profileArea__jonwrapper {
  position: relative;
  width: 100%;
  height: 25vw;
}

.profileArea__jon {
  position: absolute;
  right: 0;
  left: 0;
  margin: 0 auto;
  border-radius: 50%;
  width: 25vw;
  @media --mobile {
    border: 2px solid $neutral;
  }
  @media --pad {
    border: 3px solid $neutral;
  }
  @media -desktop {
    border: 5px solid $neutral;
  }
}

.profileArea__title {
  top: 15vh;
  padding: 10px 0 200px;
  font-size: 4vw;
  color: #edeeef;
  color: var(--niceGrey);
  width: 100%;
  text-align: center;
  font-weight: 200;
}

.profileArea__profile {
  height: 0px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 10vh;
  width: 100%;
  border-radius: 50%;
}
